<template>
    <div class="news">
        <Navbar title="新闻列表"></Navbar>
        <div class="newsList">
            
            <ul>
                <li v-for="(item,index) of list" :key="item._id">
                    <router-link :to='{name:"detail",query:{id:item._id}}'>
                        <h3>{{index + 1}}.{{item.title}}</h3>
                        <p>{{item.des}}</p>
                        <span>发布时间:{{item.time | converTime('YYYY--MM--DD,h:mm:ss a')}}</span>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:'NewList',
    data(){
        return {
            list:this.list
        }
    },
    created(){
        this.$axios({
            url:'/api/home',
            params:{_page:1,_limit:8}
        }).then(
            res=>{
                console.log(res.data)
                this.list = res.data.data
                }
        )
    }
}
</script>
<style scoped>

     .newsList{height:10rem;}
     .newsList ul{ padding:0 0.4rem;}
     .newsList ul li{ color:#494d4d; padding:1rem 0; border-bottom:.01rem solid #ccc;}
     .newsList ul li h3{max-height:1.2rem; font-size:1rem; overflow:hidden;}
     .newsList ul li p{max-height:1.8rem; margin-top:0.05rem;overflow:hidden;text-indent: 1rem;margin-top:.5rem;color:#FBA56D}
     .newsList ul li span{float:right}
     .newsList{clear: both;margin-bottom:1rem;}
</style>
