<template>
    <div class="reg">
        <h3>注册</h3>
        <van-cell-group class="dl">
            <van-field
                v-model="username"
                required
                clearable
                label="用户名"
                right-icon="question-o"
                placeholder="请输入用户名"
                @click-right-icon="$toast('question')"
            />

            <van-field
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入密码"
                required
            />
        </van-cell-group>
        <van-button type="primary" size="large" class="btn1" @click="reg">点击注册</van-button>
    </div>
</template>

<script>
export default {
    name:'Reg',
    data(){
        return {
            username:"",
            password:""
        }
    },
    methods:{
        reg(){
        this.$axios({
            url:'/api/reg',
            method:'post',
            data:{username:this.username,password:this.password}
        }).then(
            res=>{
            if(res.data.err===0){
                console.log(res.data)
                this.$router.push('/login')
            }else{
                console.log(res.data)
                this.mess = res.data.mess
            }
            }
        )
        }
    }
}
</script>
<style scoped>
    h3{
        font: 2.4rem/3rem "";
        text-align: center
    }
    .reg{
        margin-top:8rem
    }
    .dl{
        margin-top:2rem
    }
    .btn1{
        margin-top:2rem
    }
</style>