<template>
  <div class="detail">
    <Navbar title="新闻详情"></Navbar>

    <div class="item">
      <p>{{detail.title}}</p>
      <div>
        <span>作者：{{detail.detail&&detail.detail.auth}}</span>
        <span>添加时间：{{detail.time|converTime('YYYY--MM--DD,h:mm:ss a')}}</span>
      </div>
    </div>
    <div class="divider"></div>
    <div class="detail-content" v-html="detail.detail&&detail.detail.content"></div>
  </div>
</template>

<script>
export default {
    name:'NewsDetail',
    data(){
      return {
        detail:""
      }
    },
    props:['title'],
    beforeCreate(){
      let id = this.$route.query.id
      this.$axios({
        url:`/api/home/${id}`,
        params:{_page:1,_limit:7}
      }).then(
        res=>{
          this.detail=res.data.data

        })
        .catch(err=>{
          console.log(err)
        })
      
    }
}
</script>

<style scoped>
.detail{
    padding-bottom: 10rem;
    
}
.item p {
  color:#66ccff;
  font-size:1.8rem;
  font-weight:600;
  text-align: center
}
.item span{
  margin-right: 20px;
  color:#999;
  text-align: center
} 
.divider{
  width:100%;
  height:1px;
  background-color: #B3B3B3;
  margin:.3rem 0;
}
.detail-content{
  padding: .2rem 2rem;
  font:2.4rem/2rem "";
  text-indent: 2rem
}
</style>
