import Vue from 'vue'
import Router from 'vue-router'

//引入
import Home from '../src/components/Home/Home.vue'
import Search from '../src/components/Search/Search.vue'
import Shopping from '../src/components/Shopping/Shopping.vue'
import Vip from '../src/components/Vip/Vip.vue'
import NewsList from '../src/components/Home/News/NewsList'
import NewsDetail from '../src/components/Home/News/NewsDetail'
import PhotosList from '../src/components/Photo/Photo.vue'
import Login from '../src/components/Login/Login'
import Reg from '../src/components/Reg/Reg'
// src\components\Home\News\NewsDetail.vue

Vue.use(Router)
//使用vue-router插件 vue.prototype.$router = Router

export default new Router({
  //匹配的路由规则
  linkActiveClass:'link-active',
  routes: [
    {path:'/home',name:'home',component:Home},
    {path:'/search',name:'search',component:Search},
    {path:'/shopping',name:'shopping',component:Shopping},
    {path:'/vip',name:'vip',component:Vip},
    {path:'/login',name:'login',component:Login},
    {path:'/reg',name:'reg',component:Reg},
    {path:'/news/list',name:'news.list',component:NewsList},
    {path:'/',redirect:'/home'},
    {path:'/news/detail',name:'detail',component:NewsDetail,prop:{title:"新闻详情"}},
    {path:'/photo/list/:categoryId',name:'photo.list',component:PhotosList},
    
    
  ]
})
